<template>
	<view class="fieldwork-list-container">
		<view v-if="!data || data.length === 0" class="empty-state">
			<uni-icons type="location" size="48" color="#cbd5e1" />
			<text class="empty-text">暂无排污口信息</text>
			<text class="empty-tip">点击下方按钮添加</text>
		</view>

		<view v-else class="fieldwork-list">
			<view v-for="(item, index) in data" :key="item.id || index" class="fieldwork-item">
				<view v-if="item.photos && item.photos.length > 0" class="item-photos">
					<image v-for="(photo, pIndex) in item.photos.slice(0, 3)" :key="pIndex" :src="photo"
						class="item-photo" mode="aspectFill" @tap="previewImage(item.photos, pIndex)" />
					<view v-if="item.photos.length > 3" class="photo-more">
						+{{ item.photos.length - 3 }}
					</view>
				</view>

				<view class="item-content">
					<view class="item-field">
						<text class="field-label">排污口名称：</text>
						<text class="field-value">{{ item.name || '未填写' }}</text>
					</view>
					<view class="item-field">
						<text class="field-label">编号：</text>
						<text class="field-value">{{ item.code || '未填写' }}</text>
					</view>
					<view class="item-field">
						<text class="field-label">污染因子：</text>
						<text class="field-value">{{ item.pollutionFactors || '未填写' }}</text>
					</view>
				</view>

				<view class="item-actions">
					<button class="action-btn action-btn--edit" @tap="$emit('edit', index)">
						<uni-icons type="compose" size="16" color="#166534" />
						<text>编辑</text>
					</button>
					<button class="action-btn action-btn--delete" @tap="confirmDelete(index)">
						<uni-icons type="trash" size="16" color="#dc2626" />
						<text>删除</text>
					</button>
				</view>
			</view>
		</view>

		<view class="add-button-container">
			<button class="btn btn--primary" @tap="$emit('add')">
				<uni-icons type="plus" size="16" color="#ffffff" />
				<text>新增排污口</text>
			</button>
		</view>
	</view>
</template>

<script>
	export default {
		name: 'FieldworkOutletList',
		props: {
			data: {
				type: Array,
				default: () => []
			}
		},
		methods: {
			previewImage(photos, current) {
				uni.previewImage({
					urls: photos,
					current: current
				})
			},
			confirmDelete(index) {
				uni.showModal({
					title: '确认删除',
					content: '确定要删除这条排污口信息吗？',
					success: (res) => {
						if (res.confirm) {
							this.$emit('delete', index)
						}
					}
				})
			}
		}
	}
</script>

<style lang="scss" scoped>
	@import './fieldwork-common.scss';
</style>
